 /* STYLE INSERIR CONTEÚDO ANTES OU DEPOIS */
*,
:after,
:before {
  box-sizing:border-box
}

/* STYLE RAIZ DA ÁRVORE : HTML  */
:root {
  font-size:14px}

/* STYLE ESTRUTURA DA PÁGINA  */
html, body {
  height:100%;
  width:100%
}

/* STYLE CORPO DA PÁGINA  */
body{
  --color-text:#000;
  --color-text-alt:#000;
  --color-bg:#de5f40;
  --color-link:#000;
  --color-link-hover:#000;
  --color-link-alt:#000;
  --color-type:#000;
  --type-line-opacity:0.05;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
  background-color:var(--color-bg);
  color:var(--color-text);
  font-family:halyard-display,sans-serif;
  margin:0
}

/*STYLE JS LOADING DEPOIS E ANTES */
.js .loading:after,.js .loading:before{
  content:"";
  position:fixed;
  z-index:1000
}

/*STYLE JS LOADING ANTES */
.js .loading:before{
  background:var(--color-bg);
  height:100%;left:0;top:0;width:100%
}

/*STYLE LOADING DEPOIS */
.js .loading:after{
  animation:loaderAnim .7s linear infinite alternate forwards;
  background:var(--color-link);
  border-radius:50%;
  height:60px;
  left:50%;
  margin:-30px 0 0 -30px;
  opacity:.4;
  top:50%;width:60px
}


/*STYLE: DESCREVENDO COMO O ELEMENTO ANIMADO VAI RENDERIZAR, TEMPO, E SEQUENCIA */

@keyframes loaderAnim{
    to{opacity:1;transform:scale3d(.5,.5,1)}
  }

/*STYLE:  LINK */
a{
    color:var(--color-link);
    text-decoration:underline
  }

/*STYLE : LINK E PASSANDO O MOUSE SOBRE O LINK */
a,a:hover{
  outline:none
}

/*STYLE : PASSANDO O MOUSE SOBRE O LINK */
a:hover{
  color:var(--color-link-hover)
}

/*STYLE : PASSANDO O MOUSE SOBRE O LINK */
a.link-alt,a:hover{
  text-decoration:none
}

/*STYLE : PASSANDO O MOUSE SOBRE O LINK */
a.link-alt{
  color:var(--color-link-alt)
}

/*STYLE : RECEBE FOCO */
a:focus{
  background:#d3d3d3;
  outline:none
}

/*STYLE : NÃO RECEBE FOCO */
a:focus:not(:focus-visible){
  background:transparent
}

/*STYLE : RECEBE FOCO VISÍVEL */
a:focus-visible{
  background:transparent;
  outline:2px solid red
}

/*STYLE : CONTEÚDO PRINCIPAL DA PÁGINA */
main{
    overflow:hidden;
    position:relative
  }

/*STYLE :BOTÃO */
.unbutton{
    background:none;
    border:0;
    font:inherit;
    margin:0;
    padding:0
  }

/*STYLE :BOTÃO FOCADO */
.unbutton:focus{
    outline:none
  }

/*STYLE : SUB JANELAS */ 
.frame{
    display:flex;
    flex-wrap:wrap;
    margin-bottom:2rem;
    padding:1rem;
    position:relative;
    text-transform:uppercase;
    z-index:1000
  }

/*STYLE : TITULO DA SUB JANELAS */ 
.frame__title{
  font-size:1rem;
  margin:0 0 1rem
}

/*STYLE : TITULO DA SUB JANELAS */ 
.frame__tagline{
  color:var(--color-text-alt)
}

/*STYLE : LINKS DA SUB JANELAS */ 
.frame__links{
  display:inline
}

/*STYLE : NAO TITULO DA SUB JANELAS */ 
.frame__links a:not(:last-child){
  margin-right:1rem
}

/*STYLE : AUTOR DA SUB JANELAS */ 
.frame__author{
  display:block;
  margin-bottom:1rem;
  width:100%
}

/*STYLE : LINKS DA PRIMEIRA PAGINA + IMAGE */ 
.item-wrap{
  display:grid;
  grid-template-columns:50% 50%;
  text-transform:uppercase
}

/*STYLE : CARDS DA PRIMEIRA PAGINA*/ 
.item{
  --base:7vh;
  --interval:9vh;
  cursor:pointer;
  flex:none;
  margin:1rem 0;
  padding:1rem;
  position:relative;
  width:100%;
  will-change:transform,opacity
}

/*STYLE : IMAGENS DO CARDS DA PRIMEIRA PAGINA */
.item__img{
  border-radius:12px;
  width:100%;
  will-change:transform
}

/*STYLE : TITULOS DOS CARDS*/
.item__caption-title{
  font-size:inherit
}

/*STYLE : DESCRICAO DOS CARDS DA PAGINA 1*/
.item__caption-description{
  display:none
}

/*STYLE : TIPO DA ANIMAÇÃO */
.type {
  align-content:center;
  display:grid;
  height:100vmax;
  justify-content:center;
  margin-top:-50vmax;
  position:fixed;
  text-align:center;
  text-transform:uppercase;
  top:50%;
  width:100vmax;
  will-change:transform
}

/*STYLE : LINHA DECORAÇÃO DO TEXTO */
.type__line {
  -webkit-touch-callout:none;
  color:var(--color-type);
  font-family:quiche-sans,sans-serif;
  font-size:20vh;
  font-size:clamp(7rem,18.75vh,15rem);
  font-weight:700;
  line-height:.75;opacity:
  var(--type-line-opacity);
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
  white-space:nowrap;
  will-change:transform,opacity}


/*STYLE : COMPOSIÇAO INDEPENDENTE DA PAGINA */
.article-wrap {
  align-self:end;
  justify-self:center;
  padding:1rem;
  top:5vh}
  
/*STYLE : EDICAO DA PAGINA 2 */
.article, .article-wrap {
  pointer-events:none;
  position:absolute;
  width:100%
}

/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA */
.article {
  align-items:start;
  display:grid;
  grid-template-areas:"img img" "number title" "intro intro" "desc desc";
  grid-template-columns:1rem 1fr;
  opacity:0
}
/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA */
.article--current {
  opacity:1;
  pointer-events:auto;
  position:relative
}
/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA IMAGEM GERAL*/
.article__img-wrap{
  border-radius:17px 17px 0 0;
  grid-area:img;
  height:250px;
  margin-bottom:
  1rem;
  overflow:hidden;transform-origin:50% 100%;
  width:100%
}

/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA IMAGEM GERAL */
.article__img{
  background-position:50% 35%;
  background-size:cover;
  height:100%;
  width:100%
}

/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA NUMERO */
.article__number{
  font-weight:700;
  grid-area:number
}

/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA > TITULO  */
.article__title{
  pointer-events:default;
  color:var(--color-type);
  font-family:quiche-sans,sans-serif;
  font-size:8vw;
  font-weight:700;
  grid-area:title;
  line-height:.85;
  margin:1rem 0 .5rem;
  position:relative;
  text-transform:uppercase}

/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA  > SUB TEXTO */
.article__intro{
  font-weight:700;
  grid-area:intro;
  padding-right:2rem}

/*STYLE : COMPOSIÇÃO INDEPENDENTE DA PAGINA > TEXTO */
.article__description {
	grid-area: desc;
	overflow: hidden;
	padding-right: 2rem;
}

/*STYLE : BOTAO VOLTAR */
.back{
  stroke:#000;
  background:none;
  border:0;
  cursor:pointer;
  left:0;margin:0 0 1rem;
  opacity:0;padding:0;
  pointer-events:none;
  top:0;
  width:50px;
  z-index:1000
}

/*STYLE : BOTAO BACK GRAFICO */
.back svg{
  stroke-linecap:round
}

/*STYLE : BOTAO > FOCO E MOUSE POR CIMA */
.back:focus,.back:hover{
  stroke:#823725;
  outline:none
}

/*STYLE : MEDIA SCREEN > DETERMINANDO UM ESTILO ANIMACAO CSS PARA CADA TIPO DE MIDIA  */
@media screen and (min-width:53em){

/*STYLE : RAIZ DO HTML */
:root{
  font-size:16px
}

body,html{
  overflow:hidden
}

/*STYLE : SUB JANELAS */ 
.frame{
  align-content:space-between;
  display:grid;
  grid-template-areas:"title author links";grid-template-columns:40% 20% 40%;
  left:0;
  margin:0;
  max-width:none;
  padding:1rem 2vw;
  position:fixed;
  text-align:left;
  top:0;width:100%;
  z-index:100
}

/*STYLE : CLASSE TITULO INICIO DA 1 PAGINA */ 
.frame__title-wrap{
  grid-area:title;
  text-align:
  left
}
/*STYLE : TITULO INICIO DA 1 PAGINA */
.frame__title{
  margin:0
}

/*STYLE : TITULO 2 INICIO DA 1 PAGINA */
.frame__tagline{
  position:relative
}
/*STYLE : AUTOR */
.frame__author{
  grid-area:author;
  justify-self:center
}
/*STYLE : LINK */
.frame__links{
  grid-area:links;
  justify-self:end;
  padding:0
}

/*STYLE : CLASSE DA PAGINA 1 */
.item-wrap{
  align-items:center;
  display:flex;
  height:100vh;
  justify-content:flex-start;
  overflow:hidden;
  position:relative;
  width:100vw
}
/*STYLE :CARD DA PAGINA 1 */
.item{
  margin:auto 2vw var(--base);
  padding:0;
  width:22vw
}


              /*STYLE JAVASCRIPT :*/
/*STYLE JAVASCRIPT :*/
.item:nth-child(2){
  margin-bottom:calc(var(--base) + var(--interval))
}

.item:nth-child(3){
  margin-bottom:calc(var(--base) + var(--interval)*2)
}
  
.item:nth-child(4){
  margin-bottom:calc(var(--base) + var(--interval)*3)
}

.item__caption-description{
  display:block
}
  
.back{
  left:calc(31vw - 140px + 1.5rem);
  position:absolute
}

.article-wrap{
  height:80vh;
  padding:0;
  top:20vh
}
  
.article{
  grid-template-columns:1.5rem 30% 1fr 1.5rem;
  grid-template-rows:10vw 2rem 12vw auto auto;
  height:100%;
  margin:0 auto;
  width:calc(38vw + 280px);
  grid-template-areas:"... ... img ..." 
                      "number ... img ..." 
                      "... title img ..." 
                      "... intro img ..." 
                      "... desc img ...";
}
  
.article__title{
  grid-column-end:4;
  margin:0
}
  
.article__intro{
  margin:1.5rem 0
}
  
.article__img-wrap{
  align-self:end;
  height:100%;
  margin:0;
  width:100%
}

.article__img {
  background-position:50% 50%
}
}